import { ComponentsUtils } from '../../utils/components-utils';
import { CurrentSiteMapService } from './current-site-map.service';
import { Injectable, OnDestroy } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { Observable, Subject } from 'rxjs';
import { SiteMapDto } from '../dto/site-map-dto';
import { map, takeUntil, filter, debounceTime, shareReplay } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class MatchedRouteService implements OnDestroy {
  private onDestroyed$ = new Subject<void>();
  public readonly matchedRoute$: Observable<MatchedRoute[]>;

  constructor(currentSiteMap: CurrentSiteMapService,
    private readonly router: Router) {
    const siteMapServiceBLOC = new SiteMapServiceBLOC(
      currentSiteMap.getCurrentSiteMap(),
      this.router.events.pipe(
        takeUntil(this.onDestroyed$),
        filter((event): event is NavigationEnd => event instanceof NavigationEnd),
        debounceTime(100),
      )
    );

    this.matchedRoute$ = siteMapServiceBLOC.getCurrentSiteMap().pipe(shareReplay(1));
  }


  ngOnDestroy(): void {
    ComponentsUtils.onComponentDestroyed(this.onDestroyed$);
  }

}

export class MatchedRoute {
  constructor(public readonly siteMap: SiteMapDto,
    public readonly url: string) { }

}


export class SiteMapServiceBLOC {
  constructor(private readonly sitemap: SiteMapDto,
    private readonly navigationEndEvents: Observable<NavigationEnd>) {
  }

  /**
   * getCurrentSiteMap
   */
  public getCurrentSiteMap() {
    return this.navigationEndEvents
      .pipe(
        map(navigationEndEvent => navigationEndEvent.urlAfterRedirects),
        map(currentUrl => {
          return this.findSiteMap(currentUrl, [new MatchedRoute(this.sitemap, '')])
        }),
      );
  }

  private findSiteMap(url: string, acc: MatchedRoute[]): MatchedRoute[] {
    const last = acc[acc.length - 1];
    const { siteMap, url: matchedUrl } = last;
    const children = (siteMap.child || []).filter(child => SiteMapDto.isEnabled(child));
    if (!children || children.length === 0) {
      return acc;
    }

    const unmatchedUrl = url.replace(matchedUrl, '');
    const matchedChild = children.find(eachChild => unmatchedUrl.startsWith(eachChild.url));
    if (!matchedChild) {
      return acc;
    }

    const newAcc = [...acc, new MatchedRoute(matchedChild, matchedUrl + matchedChild.url)]
    return this.findSiteMap(url, newAcc);
  }
}
