import { Router, ActivatedRoute } from '@angular/router';
import { MatchedRoute } from './../../services/matched-route.service';
import { Component, OnInit, ChangeDetectionStrategy, Input } from '@angular/core';
import {SiteMapDto} from '../../dto/site-map-dto';

@Component({
  standalone: false,
  selector: 'app-site-map-list',
  templateUrl: './site-map-list.component.html',
  styleUrls: ['./site-map-list.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SiteMapListComponent implements OnInit {

  @Input()
  matchedRoute: MatchedRoute;

  constructor(private router: Router,
     private route: ActivatedRoute) { }

  ngOnInit(): void {
  }

  get childRoutes() {
    return this.matchedRoute.siteMap.child.filter(value => SiteMapDto.isEnabled(value));
  }

  public navigate(url: string) {
    this.router.navigate([url], {relativeTo: this.route});
  }
}
