import { ComponentsUtils } from './../../../utils/components-utils';
import { MatchedRouteService, MatchedRoute } from './../../services/matched-route.service';
import { Component, OnInit, ChangeDetectionStrategy, OnDestroy } from '@angular/core';
import { Subject, Observable } from 'rxjs';

@Component({
  standalone: false,
  selector: 'app-bread-crubs',
  templateUrl: './bread-crubs.component.html',
  styleUrls: ['./bread-crubs.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BreadCrubsComponent implements OnInit, OnDestroy {
  private readonly onDestroyed$ = new Subject<void>();

  matchedRoute$: Observable<MatchedRoute[]>;

  constructor(private readonly matchedRouteService: MatchedRouteService) { }

  ngOnInit(): void {
    this.matchedRoute$ = this.matchedRouteService.matchedRoute$
  }

  ngOnDestroy(): void {
    ComponentsUtils.onComponentDestroyed(this.onDestroyed$);
  }

}
