import { map } from 'rxjs/operators';
import { ComponentsUtils } from './../../../utils/components-utils';
import { MatchedRouteService } from './../../services/matched-route.service';
import { Subject, Observable } from 'rxjs';
import { Component, OnInit, ChangeDetectionStrategy, OnDestroy } from '@angular/core';
import { MatchedRoute } from '../../services/matched-route.service';

@Component({
  standalone: false,
  selector: 'app-site-map-router',
  templateUrl: './site-map-router.component.html',
  styleUrls: ['./site-map-router.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SiteMapRouterComponent implements OnInit, OnDestroy {
  private readonly onDestroyed$ = new Subject<void>();

  matchedRoute$: Observable<MatchedRoute>;

  constructor(private readonly matchedRouteService: MatchedRouteService) { }

  ngOnInit(): void {
    this.matchedRoute$ = this.matchedRouteService.matchedRoute$.pipe(
      map(matchedRoutes => {
        if(matchedRoutes.length === 0) {
          return null;
        } else {
          return matchedRoutes[matchedRoutes.length - 1];
        }
      })
      )
  }

  ngOnDestroy(): void {
    ComponentsUtils.onComponentDestroyed(this.onDestroyed$);
  }
}
