import { Directive, OnInit, TemplateRef, ViewContainerRef, EmbeddedViewRef } from '@angular/core';

export class IfVisibleDirectiveContext {
  constructor(public isVisible: boolean) { }
}

@Directive({
  standalone: false,
  selector: '[appIfVisible]'
})
export class IfVisibleDirective implements OnInit {
  private embeddedViewRef: EmbeddedViewRef<IfVisibleDirectiveContext>;
  constructor(private readonly templateRef: TemplateRef<IfVisibleDirectiveContext>,
    private readonly viewContainerRef: ViewContainerRef) { }


  ngOnInit(): void {
    this.embeddedViewRef = this.viewContainerRef.createEmbeddedView(this.templateRef, new IfVisibleDirectiveContext(false));
    const callBack = (entries, t) => {
      entries.forEach(entry => {
        if(this.embeddedViewRef.context.isVisible !== entry.isIntersecting){
          this.embeddedViewRef.context.isVisible = entry.isIntersecting;
          this.embeddedViewRef.markForCheck();
        }

      });
    };

    const observer = new IntersectionObserver(callBack, {
      rootMargin: '0px',
      threshold: 0
    });

    const target = this.embeddedViewRef.rootNodes[0]
    observer.observe(target);
  }


}
