import {filter, map, switchMap, takeUntil} from 'rxjs/operators';
import {Subject} from 'rxjs';
import {
  ActivatedRoute,
  NavigationEnd,
  RouteConfigLoadEnd,
  RouteConfigLoadStart,
  Router
} from '@angular/router';
import {Component, OnInit, OnDestroy} from '@angular/core';
import {Meta, Title} from '@angular/platform-browser';

@Component({
  standalone: false,
  selector: 'app-common-page-layout',
  templateUrl: './common-page-layout.component.html',
  styleUrls: ['./common-page-layout.component.scss']
})
export class CommonPageLayoutComponent implements OnInit, OnDestroy {
  private readonly onDestroy$ = new Subject<void>();

  isLoadingRoute = true;

  constructor(private readonly router: Router,
              private readonly activatedRoute: ActivatedRoute,
              private readonly titleService: Title,
              private readonly metaService: Meta) {
  }

  ngOnInit(): void {
    this.router.events
      .pipe(takeUntil(this.onDestroy$))
      .subscribe(event => {
        if (event instanceof RouteConfigLoadStart) {
          this.isLoadingRoute = true;
        } else if (event instanceof RouteConfigLoadEnd) {
          this.isLoadingRoute = false;
        }
      });

    this.router.events
      .pipe(takeUntil(this.onDestroy$))
      .pipe(
        filter(event => event instanceof NavigationEnd),
        map(() => this.activatedRoute),
        map(route => {
          while (route.firstChild) {
            route = route.firstChild
          }
          return route;
        }),
        switchMap(route => route.data),
      )
      .subscribe(data => {
        const {title = 'ProgTools', description = ''} = data;
        this.titleService.setTitle(title);
        this.metaService.updateTag({name: 'description', content: description})
      });
  }

  ngOnDestroy(): void {
    this.isLoadingRoute = false;
    this.onDestroy$.next();
    this.onDestroy$.complete();
  }

}
