import { NgModule } from '@angular/core';
import { Routes, RouterModule,ExtraOptions  } from '@angular/router';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  {
    path: 'finance',
    loadChildren: () =>
      import('./finance/finance.module').then(m => m.FinanceModule)
  },
  {
    path: 'home',
    loadChildren: () => import('./home/home.module').then(m => m.HomeModule)
  },
  { path: 'webrtc', loadChildren: () => import('./webrtc/webrtc.module').then(m => m.WebrtcModule) },
  { path: 'camera', loadChildren: () => import('./camera/scanner.module').then(m => m.ScannerModule) },
  { path: 'time', loadChildren: () => import('./time/time.module').then(m => m.WorldTimeModule) },
  { path: 'singaporeNric', loadChildren: () => import('./singapore-nric/singapore-nric.module').then(m => m.SingaporeNricModule) },
  { path: 'encoding', loadChildren: () => import('./modules/encoding/encoding.module').then(m => m.EncodingModule) },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
